import React, { useState, useEffect } from "react";
import Markdown from "markdown-to-jsx";
import "./blogpost.css";
import { GithubGist } from "react-gistlab";
import Loader from "../../components/loader/loader";
// var Gist = require("react-gistlabs");
import Gist from "./gist";
import { Offline, Online, Detector } from "react-detect-offline";
const moments = require("moment");

const entries = require("../../entryfile/entryfile").entries.posts;
export default function Blogpost(props) {
  // const [Data, setData] = useState([]);
  const [markdown, setmarkdown] = useState("");
  const [entry, setentry] = useState({});

  const [onceoffline, setonceoffline] = useState(false);
  useEffect(() => {
    console.log("id- ");
    console.log(props.match.params.id);
    const id = props.match.params.id;
    console.log("title- ");
    console.log(props.match.params.title);

    setentry(entries[id]);

    const mdfile = require(`../../entries/${entries[id].path}.md`);
    fetch(mdfile)
      .then(res => res.text())
      .then(result => {
        setmarkdown(result);
      })
      .catch(err => {
        console.log(err);
      });
  }, []);

  return (
    <div className="blogpostcontainer">
      <Detector
        onChange={con => {
          console.log("con connect;assdkak");
          console.log(con);
          setonceoffline(true);
        }}
        render={({ online }) => <div></div>}
      />
      <Offline>
        <div className="offlinediv">You appears to be offline</div>
      </Offline>
      <Online>
        <div hidden={!onceoffline} className="onlinediv">
          Welcome Back
        </div>
      </Online>

      <div class="container ">
        <div class="row">
          <div class="col-lg-10">
            <h1 class="mt-4">{entry.title}</h1>

            <hr />
            {console.log(new Date().toISOString())}
            <p>Posted {moments(entry.postedon).fromNow()} </p>

            <hr />

            <Markdown
              children={markdown}
              options={{
                overrides: {
                  GithubGist: {
                    component: Gist
                  }
                }
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
