import React, { Component } from "react";
import Markdown from "markdown-to-jsx";
import "./card.css";

const moments = require("moment");
class Card extends Component {
  state = {};

  componentWillMount() {
    console.log(this.props);
  }

  render() {
    return (
      <div className="card mb-4 ">
        <div className="card-body postcard">
          <h2 className="card-title">{this.props.title}</h2>
          <p className="card-text  ">
            {/* <Markdown>{this.props.description}</Markdown> */}

            <Markdown children={this.props.description} />
          </p>
          <a
            href={`/post/${this.props.index}/${this.props.title}`}
            class="btn btn-outline-primary"
          >
            Read More &rarr;
          </a>
        </div>
        <div class="card-footer postcard text-muted">
          Posted {moments(this.props.postedon).fromNow()}
        </div>
      </div>
    );
  }
}

export default Card;
