import React, { useState, useEffect } from "react";
import { Offline, Online, Detector } from "react-detect-offline";
import Card from "../../components/card/card";
import "./blog.css";
const entries = require("../../entryfile/entryfile").entries.posts;

export default function Blog(props) {
  const [Data, setData] = useState([]);
  const [onceoffline, setonceoffline] = useState(false);

  const [timediff, settimediff] = useState(0);
  const [timeoutsync, settimeoutsync] = useState(true);

  const getpost = () => {
    return new Promise((resolve, reject) => {
      var postarr = [];
      entries.forEach(element => {
        const mdfile = require(`../../entries/${element.path}.md`);
        postarr.push(fetch(mdfile));
      });
      console.log("postss");
      resolve(postarr);
    });
  };

  //   var callMyPromise = async () => {
  //     var result = await getpost();
  //     //anything here is executed after result is resolved
  //     return result;
  //   };

  useEffect(() => {
    // callMyPromise()

    fetch("http://worldtimeapi.org/api/ip")
      .then(res => res.json())
      .then(result => {
        console.log(result.unixtime);

        var timeDiff = Math.abs(Date.now() / 1000 - result.unixtime);
        console.log("timediff", timeDiff);
        settimediff(timeDiff / 60);
        if (timeDiff > 600) {
          settimeoutsync(false);
          console.log("Data appears to be " + timeDiff / 60 + " minutes old.");
        }
      })
      .catch(err => {
        console.log(err);
      });

    getpost()
      .then(result => {
        // console.log(result);
        var pss = Promise.all(result);
        pss
          .then(function(results) {
            console.log("my promise");
            console.log(results);
            result.map((sigle, index) => {
              sigle
                .then(ress => ress.text())
                .then(result => {
                  console.log("aaaaa");
                  // console.log(result);
                  setData(pre => {
                    return [
                      ...pre,
                      {
                        title: entries[index].title,
                        description: String(result).slice(0, 200),
                        postedon: entries[index].postedon
                      }
                    ];
                  });
                })
                .catch(err => {
                  console.log(err);
                });
            });
            //
            //   res.json(result);
          })
          .catch(err => {
            console.log(err);
          });
      })
      .catch(err => {});

    // getpost()
    //   .then(data => {
    //     console.log(data);
    //     setData(data);
    //   })
    //   .catch(err => console.log(err));
  }, []);

  return (
    <div className="maindiv">
      <Detector
        onChange={con => {
          console.log("con connect;assdkak");
          console.log(con);
          setonceoffline(true);
        }}
        render={({ online }) => <div></div>}
      />
      <Offline>
        <div className="offlinediv">You appears to be offline</div>
      </Offline>
      <Online>
        <div hidden={!onceoffline} className="onlinediv">
          Welcome Back
        </div>
      </Online>
      <div className="timesync" hidden={timeoutsync}>
        <a href="https://time.is" target="_blank">
          your time appears to be {Math.floor(timediff)} minutes off . Is your
          clock out of sync?
        </a>
      </div>
      <div class="container">
        <div class="row">
          <div class="col-md-8">
            <h1 class="my-4">
              Blogged by &ensp;
              <span className="smlltextblogheading">rajitha</span>
            </h1>
            {console.log("data", Data[0])}
            {Data.map((element, index) => {
              return (
                <Card
                  title={element.title}
                  description={element.description}
                  postedon={element.postedon}
                  index={index}
                />
              );
            })}
            {/* <ul class="pagination justify-content-center mb-4">
              <li class="page-item">
                <a class="page-link" href="#">
                  &larr; Older
                </a>
              </li>
              <li class="page-item disabled">
                <a class="page-link" href="#">
                  Newer &rarr;
                </a>
              </li>
            </ul> */}
          </div>

          {/* <div class="col-md-4">
            <div class="card my-4 sidecomponent">
              <h5 class="card-header">Side Widget</h5>
              <div class="card-body">
                You can put anything you want inside of these side widgets. They
                are easy to use, and feature the new Bootstrap 4 card
                containers!
              </div>
            </div>
          </div> */}
        </div>
      </div>{" "}
      <footer class="py-3 ">
        <div class="container">
          <p class="m-0 text-center text-white">
            Forged by the Dark Lord Rizky, in the Fires of Mount Doom
          </p>
        </div>
      </footer>
    </div>
  );
}
